* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

.bg-header:before,
.bg-header:after %extend_1 {
    content: "";
    position: absolute;
    bottom: -1rem;
    height: 1rem;
    width: 30%;
    background-color: red;
}

html {
    font-size: 16px;
    line-height: 1.6;
    font-weight: 400;
}